@font-face {
  font-family: "ArchivoBlack";
  src: url("./fonts/ArchivoBlack-Regular.ttf");
}

* {
  color: darkblue;
  font-family: "ArchivoBlack", 'Arial Bold', Gadget, sans-serif, monospace;
}

html {
  width:100%;
  height:100%;
  display:table;
  text-align:center;
  overflow-x: hidden;
  min-width: 880px;
}

body {
  background-color: black;
  /* animation: translate 10s linear infinite; */
  background: url("./assets/racetrack.jpg") no-repeat, #111111;
  background-attachment: fixed;
  /* margin: auto 15%; */
  overflow-x: hidden;
  background-size: cover;
  margin: auto 0;
}

.experiences {
  margin: 10%;
}

.experiences h1 {
  font-family: "ArchivoBlack", 'Arial Bold', Gadget, sans-serif;
  font-size: 70px;
  background-clip: text;
  background-image: linear-gradient(45deg, rgb(136, 28, 28), rgb(79, 119, 192));
  background-size: 100%;
  background-repeat: repeat;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent; 
  -moz-background-clip: text;
  -moz-text-fill-color: transparent;
}

.USAFexperiences {
  background-color: #00308f;
  padding: 90px;
  margin: 20px;
  background-image: url('./assets/wavingFlag.png');
  background-size: cover;
}

.USAFexperiences:hover {
	-webkit-transform: scale(0.9);
	-moz-transform: scale(0.9);
	-ms-transform: scale(0.9);
	-o-transform: scale(0.9);
	transform: scale(0.9);
}

.USAFexperiences h2{
  display: inline-block;
  text-align: center;
  color: white;
  font-size: 50px;
  margin: 30px;
}

.Softwareexperiences {
background: linear-gradient(45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
background-size: 400% 400%;
animation: gradient 15s ease infinite;
  padding: 90px 0px 10px 0px;
  margin: 20px;
}

.Softwareexperiences:hover {
	-webkit-transform: scale(0.9);
	-moz-transform: scale(0.9);
	-ms-transform: scale(0.9);
	-o-transform: scale(0.9);
	transform: scale(0.9);
}

/*Creds to Alison Quaglia*/ 
@keyframes gradient {
0%      {background-position: 0% 50%;}
50%     {background-position: 100% 50%;}
100%    {background-position: 0% 50%;}
}

.Softwareexperiences h2 {
  display: inline-block;
  text-align: center;
  color: white;
  font-size: 50px;
  margin: 10px 0px;
}

#canvas {
  width: 95%;
  height: 70px;
  background-color: black;
  text-align: center;
}

.Hardwareexperiences {
  background-color: goldenrod;
  background-image: url("./assets/greencircuit.jpeg");
  padding: 90px;
  margin: 20px;
}

.Hardwareexperiences:hover {
	-webkit-transform: scale(0.9);
	-moz-transform: scale(0.9);
	-ms-transform: scale(0.9);
	-o-transform: scale(0.9);
  transform: scale(0.9);
}

.Hardwareexperiences h2 {
  display: inline-block;
  margin: 10%;
  text-align: center;
  color: white;
  font-size: 50px;
}

.learnMore {
  color: white;
  vertical-align: middle;
}

.HardPhoto {
  display: inline;
  height: 200px;
  width: 200px;
  vertical-align:middle;
}

.UsafPhoto {
  display: inline;
  height: 200px;
  width: 200px;
  vertical-align:middle;
  border-radius: 50%;
}

#name {
  padding-top: 1px;
  background-color: white;
  font-size: 150px;
  margin-top: 0;
  margin-bottom: 300px;
}

#name h1 {
  font-size: 130px;
  margin-top: 0px;
  font-family: "ArchivoBlack", 'Arial Bold', Gadget, sans-serif;
  background-clip: text;
  background-image: linear-gradient(45deg, #661f62, #ff3714);
  background-size: 100%;
  background-repeat: repeat;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent; 
  -moz-background-clip: text;
  -moz-text-fill-color: transparent;
}

#profilePic {
  border: 3px solid white;
  display: block;
  border-radius: 5%;
  margin: -200px auto;
}

.blur {
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
  -moz-backdrop-filter: blur(5px);
  border: 2px solid white;
  padding: 20px;
  margin: 20px;
}

.about {
  color: white;
  border:3px solid white;
  background-image: linear-gradient(45deg, #98a8f0, #690559);
  margin-top: 50px;
  margin: 50px 10%;
  padding-top: 100px;
  padding-bottom: 50px;
  text-align: center;
  border-radius: 5em;
}

.about h2 {
  margin: 50px 100px 10px 100px;
  font-family: 'Arial Bold', Gadget, sans-serif;
  color: white;
  font-size: 50px;
}

.about p {
  margin: 0 10%;
  font-family: "Helvetica";
  color: white;
  margin-bottom: 20px;
  font-size: 25px;
}

.sections {
  display: inline;
  padding: 0;
}

.sections li {
  display: inline;
  padding: 140px;
}

.sectionImages {
  display: inline-block;
  padding: 0;
}

.sectionImages li {
  display: inline-block;
  padding: 15px 15px 10px 15px;
  margin: 10px;
  background-color: white;
  border-radius: 20%;
}

.sectionImages li:hover {
  background-color: #661f62;
  background-image: linear-gradient(45deg, #f17777, #951bc5);
}

footer {
  font-family: "Helvetica";
  text-align: center;
  background-color: white;
  margin: auto 5%;
  border: 2px solid white;
  border-top-right-radius: 5em;
  border-top-left-radius: 5em;
}